import React, { useState } from "react";
import { useStyles } from "../styles";
import {
  Container,
  Typography,
  Button,
  Icon,
  Paper,
  Box,
  TextField,
  Checkbox,
  FormControlLabel,
} from "@material-ui/core";

const UrlAdd = () => {
  const classes = useStyles();
  const [fullUrl, setFullUrl] = useState("");
  const [loading, setLoading] = useState(false);

  return (
    <Paper className={classes.UrlsContainer}>
      <Box display="flex" flexDirection="column">
        <Box flexGrow={1}>
          <TextField type="url" label="Full URL" fullWidth value={fullUrl} />
        </Box>
        <Box flexGrow={1}>
          <FormControlLabel
            value={false}
            control={<Checkbox color="primary" />}
            label="Enable Logging"
            labelPlacement="end"
          />
        </Box>
        <Button
          className={classes.addUrlButton}
          startIcon={<Icon>Shrink</Icon>}
          aria-label="Shrink URL"
          disabled={!fullUrl.length > 0}
        >
          {loading ? <span aria-label="loading">...</span> : "Shrink"}
        </Button>
      </Box>
    </Paper>
  );
};

export default UrlAdd;
